const Index = () => {
    const events = [
        'routeChangeStart',
        'routeChangeComplete',
        'routeChangeError',
        'beforeHistoryChange',
        'hashChangeStart',
        'hashChangeComplete',
    ]

    function makeEvent(type) {
        return (...args) => {
            console.log(type, ...args)
        }
    }

    events.forEach(event => {
        Router.events.on(event, makeEvent(event))
    })

    function gotoTestB() {
        Router.push({
            pathname:'/test/b',
            query:{
                id:2
            }
        }, '/test/b/2')
    }
    return (
       <>
           <span>Index</span>
           <a>Index A</a>
       </>

    )
}

export default connect(function mapStateToProps(state) {
    return {
        counter: state.counter.count,
        
    }
})(Index)